/* =============================================
   INTELLIO AUTH — MOBILE RESPONSIVE FIXES
   Add these at the BOTTOM of your auth CSS
   ============================================= */

@media (max-width: 768px) {

  /* Full screen on mobile */
  .auth-wrapper {
    align-items: flex-end; /* card slides up from bottom */
    padding: 0;
    background: #fff;
  }

  .auth-card {
    border-radius: 24px 24px 0 0; /* rounded top only */
    padding: 32px 24px;
    max-width: 100%;
    width: 100%;
    box-shadow: 0 -8px 40px rgba(0,0,0,0.08);
    gap: 14px;
    /* Safe area for iPhone home bar */
    padding-bottom: max(32px, env(safe-area-inset-bottom));
  }

  .auth-logo { font-size: 30px; }

  .auth-title { font-size: 20px; }

  .auth-subtitle { font-size: 13px; }

  .auth-field input {
    font-size: 16px; /* prevents iOS auto-zoom */
    padding: 12px 14px;
  }

  .auth-btn {
    padding: 14px;
    font-size: 15px;
    border-radius: 12px;
  }
}

/* Small phones */
@media (max-width: 380px) {
  .auth-card { padding: 24px 18px; }
  .auth-title { font-size: 18px; }
}

/* Dark mode support */
body.dark .auth-wrapper { background: #0a0a0f; }
body.dark .auth-card {
  background: #111118;
  box-shadow: 0 -8px 40px rgba(0,0,0,0.4);
}
body.dark .auth-title { color: #fff; }
body.dark .auth-subtitle { color: #666; }
body.dark .auth-field label { color: #aaa; }
body.dark .auth-field input {
  background: #1a1a24;
  border-color: #2a2a35;
  color: #ddd;
}
body.dark .auth-field input:focus { border-color: #6366f1; }
body.dark .auth-btn { background: #fff; color: #111; }
body.dark .auth-btn:hover:not(:disabled) { background: #eee; }
body.dark .auth-switch { color: #666; }
body.dark .auth-switch span { color: #fff; }