/* =============================================
   INTELLIO — MOBILE RESPONSIVE FIXES
   Add these at the BOTTOM of your existing CSS
   ============================================= */

/* Fix app taking full screen on mobile */
@media (max-width: 768px) {

  /* App container — full width, no box shadow */
  .app {
    max-width: 100%;
    width: 100%;
    height: 100dvh; /* dynamic viewport height — fixes iOS bottom bar */
    border-radius: 0;
    box-shadow: none;
  }

  /* Header — tighter on mobile */
  .header {
    padding: 10px 14px;
  }

  .header-title { font-size: 14px; }
  .header-subtitle { font-size: 10px; }

  .logo-icon {
    width: 30px;
    height: 30px;
    font-size: 14px;
    border-radius: 8px;
  }

  .icon-btn {
    width: 30px;
    height: 30px;
    font-size: 13px;
    border-radius: 8px;
  }

  /* Hide status badge on small screens */
  .status-badge { display: none; }

  /* Chat area — less padding on mobile */
  .chat-area {
    padding: 16px 14px 8px;
    gap: 12px;
  }

  /* Messages — wider bubbles on mobile */
  .message-content { max-width: 88%; }

  .message-bubble {
    font-size: 13.5px;
    padding: 10px 13px;
  }

  /* Avatar — slightly smaller */
  .avatar {
    width: 24px;
    height: 24px;
    font-size: 10px;
    border-radius: 7px;
  }

  /* Empty state — compact */
  .empty-state {
    padding: 24px 16px;
    gap: 10px;
  }

  .empty-icon {
    width: 46px;
    height: 46px;
    font-size: 20px;
    border-radius: 13px;
  }

  .empty-title { font-size: 17px; }
  .empty-subtitle { font-size: 13px; }

  .suggestions { gap: 6px; }

  .suggestion-chip {
    padding: 6px 12px;
    font-size: 12px;
  }

  /* Input area — safe area for phones with home bar */
  .input-area {
    padding: 10px 14px 16px;
    padding-bottom: max(16px, env(safe-area-inset-bottom));
  }

  .input-container {
    padding: 8px 8px 8px 12px;
    border-radius: 12px;
    gap: 6px;
  }

  .input-field { font-size: 16px; } /* 16px prevents iOS auto-zoom on focus */

  .send-btn {
    width: 32px;
    height: 32px;
    font-size: 14px;
    border-radius: 8px;
  }

  .voice-btn {
    width: 32px;
    height: 32px;
    font-size: 13px;
    border-radius: 8px;
  }

  .input-footer { margin-top: 6px; }
  .input-hint { font-size: 10px; }
  .model-tag { font-size: 10px; }

  /* Sidebar — full width overlay on mobile */
  .sidebar {
    width: 80%;
    max-width: 300px;
  }

  /* Message actions — always visible on mobile (no hover) */
  .message-actions { display: flex; }

  /* Toast — higher up on mobile */
  .toast { bottom: 80px; font-size: 12px; padding: 7px 16px; }

  /* Generated images — full width */
  .generated-image { max-width: 100%; }

  /* Typing bubble */
  .typing-bubble { padding: 10px 13px; }
}

/* Extra small phones (iPhone SE, etc.) */
@media (max-width: 380px) {
  .header-title { font-size: 13px; }
  .message-content { max-width: 92%; }
  .empty-title { font-size: 15px; }
  .suggestion-chip { font-size: 11px; padding: 5px 10px; }
}

/* Fix iOS Safari 100vh bug */
@supports (-webkit-touch-callout: none) {
  .app { height: -webkit-fill-available; }
}